import { BasicColumn, FormProps } from '/@/components/Table';
import { defHttp } from '/@/utils/http/axios';
import { useUserStore } from '/@/store/modules/user';

enum Api {
  getOrgInfoUrl = '/bi/common/org/info',

  getStationUrl = '/baseset/common/list-station',

  getSafetyAgreementUrl = '/bi/safety/agreement',

  exportExcelUrl = '/bi/safety/export-agreement',
}

//导出
export const exportExcel = (params) =>
  defHttp.post(
    { url: Api.exportExcelUrl, params, responseType: 'blob' },
    { isReturnNativeResponse: true },
  );

export const getSafetyAgreementData = (params) =>
  defHttp.get({ url: Api.getSafetyAgreementUrl, params }, { isOnlyResult: true });

export const getOrgInfoData = (params) =>
  defHttp.get({ url: Api.getOrgInfoUrl, params }, { isOnlyResult: true });

export const getStationData = (params) => {
  return defHttp.post({ url: Api.getStationUrl, params });
};

export const searchFormSchema: Partial<FormProps> = {
  labelWidth: 120,
  actionColOptions: {
    span: 24,
  },
  autoAdvancedLine: 1,
  showAdvancedButton: true,
  baseColProps: {
    span: 7,
  },
  autoSubmitOnEnter: true,
  noHideBtnFlag: true,
  schemas: [
    {
      field: `orgId`,
      label: `所属公司`,
      component: 'ApiSelect',
      componentProps: {
        options: [],
        api: getOrgInfoData,
        placeholder: '请选择所属公司',
      },
      show: useUserStore().isMultiOrganization,
    },
    {
      field: `stationId`,
      label: `电站名称`,
      component: 'ApiSelect',
      componentProps: {
        options: [],
        api: async () => {
          const sts = await getStationData({});
          const map: any = [];
          sts.result.forEach((e) => {
            map.push({ value: e.powerStationId, label: e.powerStationName });
          });

          return map;
        },
        placeholder: '请输入电站名称',
      },
    },
  ],
};

export const columns: BasicColumn[] = [
  {
    title: '所属公司',
    dataIndex: 'stationOrganName',
    width: 120,
    customCell: (record) => ({
      rowSpan: record.rowOrgSpan,
    }),
    resizable: true,
    ifShow: useUserStore().isMultiOrganization,
  },
  { title: '电站名称', dataIndex: 'stationName', width: 120, resizable: true },
  // { title: '站长', dataIndex: '', width: 150 },
  { title: '在职人数', dataIndex: 'total', width: 80, resizable: true },
  { title: '应签数', dataIndex: 'total', width: 80, resizable: true },
  { title: '已签数', dataIndex: 'signTotal', width: 80, resizable: true },
  { title: '签订率(%)', dataIndex: 'rate', width: 80, resizable: true },
  { title: '未签订', dataIndex: 'persionName', width: 250, resizable: true },
];
